import {
  ICalendar,
  YahooCalendar,
  GoogleCalendar,
  OutlookCalendar
} from 'datebook'

const config = {
  title: 'Happy Hour',
  location: 'The Bar, New York, NY',
  description: 'Let\'s blow off some steam with a tall cold one!'
}

console.log('ICS: ', (new ICalendar(config)).render())

console.log('Yahoo: ', (new YahooCalendar(config)).render())

console.log('GoogleCalendar: ', (new GoogleCalendar(config)).render())

console.log('OutlookCalendar: ', (new OutlookCalendar(config)).render())